html {
  height: 100%;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: calc(100vh - 80px);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

img{max-width:100%}main{margin-top:60px}.Digital{box-sizing:border-box;padding-top:6.5rem}.Digital section{width:80%;margin:auto;display:grid;grid-gap:20px;box-shadow:3px 0px 10px 1px inset rgba(0,0,0,0.1)}.Digital section:not(:first-of-type){padding-top:3px;padding-bottom:5rem}.Digital section img{max-width:80%;margin-left:auto;margin-right:auto;margin-top:10px;margin-bottom:20px}.Digital section p{line-height:2;font-size:0.9rem;text-align:left;padding:3rem 0.97rem 5rem 0.97rem}@media screen and (min-width: 500px){.Digital section img{max-width:70%}}@media screen and (min-width: 768px){.Digital section:nth-of-type(odd){grid-template-columns:1fr 1fr;grid-template-areas:'. title' 'image content';padding-top:3rem;padding-bottom:5rem}.Digital section:nth-of-type(even){grid-template-columns:1fr 1fr;grid-template-areas:'title .' 'content image';padding-top:3rem;padding-bottom:5rem}.Digital section h3{grid-area:title}.Digital section img{grid-area:image;max-width:100%;margin:auto}.Digital section p{font-size:1rem;grid-area:content;padding:0.97rem;display:flex;align-items:center}}@media screen and (min-width: 1024px){.Digital section h3{font-size:1.5rem;letter-spacing:0.1rem}.Digital section img{max-width:80%}.Digital section p{font-size:1.2rem;padding:0 1.4rem}.Digital section:nth-of-type(5) img{max-width:70%}}.Identity{box-sizing:border-box;padding-top:3rem}.Identity section{display:grid;grid-gap:20px;box-shadow:3px 0px 10px 1px inset rgba(0,0,0,0.1)}.Identity section:not(:first-of-type){padding-top:3px;padding-bottom:5rem}.Identity section img{max-width:80%;margin-left:auto;margin-right:auto;margin-top:10px;margin-bottom:20px}.Identity section p{line-height:2;font-size:0.9rem;text-align:left;padding:3rem 0.97rem 5rem 0.97rem}@media screen and (min-width: 500px){.Identity section img{max-width:70%}}@media screen and (min-width: 768px){.Identity section:nth-of-type(odd){grid-template-columns:1fr 1fr;grid-template-areas:'image title' 'image content';padding-top:3rem;padding-bottom:5rem}.Identity section:nth-of-type(even){grid-template-columns:1fr 1fr;grid-template-areas:'title image' 'content image';padding-top:3rem;padding-bottom:5rem}.Identity section h3{grid-area:title}.Identity section img{grid-area:image;max-width:100%;margin:auto}.Identity section p{font-size:1rem;grid-area:content;padding:0.97rem;display:flex;flex-direction:column;align-items:center}}@media screen and (min-width: 1024px){.Identity section h3{font-size:1.5rem;letter-spacing:0.1rem}.Identity section img{max-width:80%}.Identity section p{font-size:1.2rem;padding:0 1.4rem}}.Identity .Personal-Branding img{max-width:50%}.About{max-width:80%;margin:auto;padding-top:2rem;padding-bottom:3rem;display:grid;text-align:left}.About img{max-width:40%;margin:auto;margin-top:5rem;padding-bottom:3rem;position:relative;z-index:-2}@media (min-width: 768px){.About{grid-template-columns:1fr 3fr;grid-gap:10%}.About img{max-width:100%;margin-top:3rem;z-index:-2}}.About .Who-we-are{line-height:1.7}.About .Who-we-are p{padding:15px 0}.About .Services{display:grid;grid-template-columns:1fr;grid-gap:30px;grid-template-areas:'title' 'content1' 'content2';margin-top:2rem;text-align:left}.About .Services h3{font-size:2rem}.About .Services h5{font-size:1.5rem;margin-bottom:0.5rem}@media (min-width: 768px){.About .Services{grid-template-columns:1fr;grid-template-rows:1fr 4fr;grid-template-areas:'title title' 'content1 content2'}.About .Services h3{font-size:1.5rem}.About .Services h5{font-size:1.2rem;margin-bottom:0.5rem}}.About .Services section:nth-of-type(1){grid-area:content1}.About .Services section:nth-of-type(2){grid-area:content2}

.toggle-button{display:flex;flex-direction:column;justify-content:space-around;height:20px;width:25px;background:transparent;border:none;cursor:pointer;margin-right:1rem;padding:0}.toggle-button:focus{outline:none}.toggle-button__line{width:25px;height:2px;background:white}

.toolbar{top:0;left:0;position:fixed;width:100%;background:#521751;height:60px}.toolbar__navigation{display:flex;height:100%;align-items:center;padding:0 1rem;justify-content:space-between}.toolbar__navigation ul{list-style:none;padding:0;margin:0;display:flex}.toolbar__navigation ul li{padding:0 0.7rem}.toolbar__navigation-items a{color:white;text-decoration:none;padding:0 0.7rem}.toolbar__navigation-items a:hover{color:green}.toolbar__logo a{color:white;text-decoration:none;font-size:1.5rem}@media (min-width: 769px){.toolbar div:first-of-type{display:none}}@media (max-width: 768px){.toolbar__navigation-items{display:none}.toolbar div:first-of-type{order:2}}

.side-drawer{height:100%;background:white;box-shadow:1px 0px 7px rgba(0,0,0,0.5);position:fixed;top:0;left:0;width:70%;max-width:400px;z-index:101;-webkit-transform:translateX(-100%);transform:translateX(-100%);-webkit-transition:-webkit-transform 0.5s ease-out;transition:-webkit-transform 0.5s ease-out;transition:transform 0.5s ease-out;transition:transform 0.5s ease-out, -webkit-transform 0.5s ease-out}.side-drawer.open{-webkit-transform:translateX(0);transform:translateX(0)}.side-drawer ul{height:100%;list-style-type:none;display:flex;flex-direction:column;margin-top:80px;padding:0}.side-drawer li{text-indent:1rem}.side-drawer li:first-of-type{border-bottom:1px solid rgba(0,0,0,0.3)}.side-drawer li:not(:first-of-type){border-bottom:1px solid rgba(0,0,0,0.3)}.side-drawer a{text-decoration:none;color:black;font-size:1.2rem;padding:1rem 0;background:purple;width:100%;display:block}@media (min-width: 769px){.side-drawer{display:none}}

.backdrop{position:fixed;height:100%;width:100%;background:rgba(0,0,0,0.3);top:0;left:0;z-index:100}

